<template>
  <v-col
    cols="12"
    :md="6"
  >
    <v-hover
      v-slot="{ hover }"
      open-delay="100"
    >
      <v-sheet
        class="d-flex testimonios__vsheet"
        color="white"
        :elevation="hover ? 5 : 2"
      >
        <div class="testimonio">
          <div class="quotas">
            <v-icon x-large>
              mdi-format-quote-open
            </v-icon>
          </div>

          <div class="texto">
            {{
              value.Comentario.length > 230
                ? value.Comentario.substring(0, 230) + '...'
                : value.Comentario
            }}
          </div>
        </div>
      </v-sheet>
    </v-hover>

    <div class="nombre">
      <h4>{{ value.Autor }}</h4>
      <!-- <h5>{{ value.Descripcion }}</h5> -->
    </div>
  </v-col>
</template>

<script>
  export default {
    name: 'CardTestimonios',
    props: {
      value: {
        Comentario: {
          type: Text,
          default: '',
        },
        Autor: {
          type: Text,
          default: '',
        },
        Descripcion: {
          type: Text,
          default: '',
        },
        type: Object,
        default: null,
      },
    },
  }
</script>

<style>
.v-image__image {
  transition: 0.3s linear;
}
.testimonios__vsheet {
  padding: 20px;
  display: flex;
  flex-direction: row;
  height: 150px;
}

.testimonio {
  display: flex;
  align-items: center;
  justify-content: center;
}

.texto,
.quotas .v-icon {
  padding: 0px;
  color: #9d9d9d;
  font-style: oblique;
  font-size: 15px;
}

.nombre {
  display: flex;
  flex-direction: column;
  margin: 20px 0px 20px 0px;
  color: #595959;
  font-size: 16px !important;
}
.nombre h5 {
  color: #36ba7f;
}

@media (max-width: 400px) {
  .testimonios__vsheet {
    height: 250px;
  }
  .texto,
  .quotas .v-icon {
    padding: 0px;
    color: #b3b3b3;
    font-style: oblique;
    font-size: 4.5vw;
  }
}

@media (max-width: 363px) {
  .testimonios__vsheet {
    height: 300px;
  }
}
</style>
